import {
    get,
    post,
    post_json
} from '@/http/axios'


export default {
    namespaced: true,
    state: {
        // 订单数据
        orderData: []
    },
    mutations: {
        SET_ORDERDATA(state, payload) {
            state.orderData = payload
        }
    },
    actions: {
        //   获取全部栏目的数据
        async getAllOrderData({
            commit
        }, payload) {
            let res = await get('/order/pageQuery', payload)
            commit('SET_ORDERDATA', res.data.data.list)
        }
    }
}